@-webkit-keyframes animBefore {
  0% {}
  top: -5rem;
  z-index: 1;
  25% {}
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  z-index: 1;
  50% {}
  top: 5rem;
  z-index: -1;
  75% {}
  background-color: $black;
  -webkit-transform: scale(0.8);
  transform: scale(0.8);
  z-index: -1;
  100% {}
  top: -5rem;
  z-index: -1; }
